.mainDiv{
    position: absolute;
    left: 50%;
    top: 10%;
    transform: translate(-50%, -10%);

    background-color: #f8f8f8;
    width: 400px;

    display: flex;
    flex-direction: column;
    gap: 10px;

    z-index: 15;
    padding: 30px;

    border-radius: 10px;
}

.mainDiv input{
    border: #FFFFFF;
    height: 30px;
    padding-left: 10px;
    border-radius: 10px;
}

.mainDiv input::placeholder{
}

.mainDiv button{
    border-radius: 10px;
    margin-top: 10px;
    font-size: 15px;
    color: #2e6da4;
    border: 1px solid #2e6da4;
    height: 30px;
    cursor: pointer;
}

.error{
    margin-top: 15px;
    font-size: 16px;
    color: red;
}

.title{
    font-size: 15px;
    text-align: center;
    margin-bottom: 10px;
    font-weight: bold;
}