th {
    text-align: left;
    padding: 8px;
    border-bottom: 1px solid #0f0f0f;
}

td {
    text-align: left;
    padding: 8px;
    border-bottom: 1px solid #e5e5e5;
}

table {
    border-top: 1px solid #0f0f0f;
    border-bottom: 1px solid #0f0f0f;
}

.table {
    position: absolute;
    left: 50%;
    top: 20%;
    transform: translate(-50%, -20%);
}

.addButton{
    color: #2e6da4;
    text-align: right;
    cursor: pointer;
}

.addButton span {
    border: 1px solid #2e6da4;
    padding: 5px 10px;
    border-radius: 10px;
}

.underModal{
    position: absolute;
    z-index: 20;
    background-color: grey;
    opacity: 1;
    height: 100vh;
    width: 100vw;
}

.success{
    margin-top: 15px;
    font-size: 16px;
    color: green;
}