.mainDiv{
    padding: 30px;
}

.buttons{
    display: flex;
    gap: 15px;
    margin-bottom: 25px;
    cursor: pointer;
    background-color: #FFFFFF;
    align-items: center;
    border-bottom: 1px solid #D7D7D7;
}

.checked{
    color: black;
    padding: 7px;
    border-radius: 10px 10px 0 0;
    border: 1px solid #D7D7D7;
    font-weight: 600;
}